const genLayoutLightStyle = token => {
  const {
    componentCls,
    bodyBg,
    lightSiderBg,
    lightTriggerBg,
    lightTriggerColor
  } = token;
  return {
    ["".concat(componentCls, "-sider-light")]: {
      background: lightSiderBg,
      ["".concat(componentCls, "-sider-trigger")]: {
        color: lightTriggerColor,
        background: lightTriggerBg
      },
      ["".concat(componentCls, "-sider-zero-width-trigger")]: {
        color: lightTriggerColor,
        background: lightTriggerBg,
        border: "1px solid ".concat(bodyBg),
        // Safe to modify to any other color
        borderInlineStart: 0
      }
    }
  };
};
export default genLayoutLightStyle;